<template>
  <div>
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>数据统计</el-breadcrumb-item>
      <el-breadcrumb-item>数据报表</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片区域 -->
    <div class="charts">
      <el-card class="resChartCard">
        <div id="resChart" style="width: 600px; height: 400px"></div>
      </el-card>
      <el-card class="adrChartCard">
        <div id="addressChart" style="width: 1000px; height: 400px"></div>
      </el-card>
    </div>
  </div>
</template>

<script>
import * as echarts from 'echarts'
import resident from '../Resident/Resident.vue'

export default {
  components: { resident },

  data() {
    return {
      onStatus: 0,
      offStatus: 0,
      residentList: [],
      addressList: [],
      areas: [
        '黄浦区',
        '徐汇区',
        '长宁区',
        '静安区',
        '普陀区',
        '虹口区',
        '杨浦区',
        '浦东新区',
        '闵行区',
        '宝山区',
        '嘉定区',
        '金山区',
        '松江区',
        '青浦区',
        '奉贤区',
        '崇明区',
      ],
      nums: [],
    }
  },
  created() {
    this.getAddressList()
    this.getResidentList()
  },
  mounted() {},
  methods: {
    //获得居民账号信息生成图表
    async getResidentList() {
      const { data: res } = await this.$axios.post(`/user/allUser`)
      if (res.status !== 0) {
        return this.$message.error('获取居民列表失败！')
      } else {
        this.residentList = res.data
      }
      this.residentList.forEach((item) => {
        if (item.resident_status === 0) this.offStatus++
        else this.onStatus++
      })
      this.initResStateCharts()
    },
    //生成居民账号状态信息图表
    initResStateCharts() {
      var myChart = echarts.init(document.getElementById('resChart'))
      var option = {
        title: {
          text: '居民账号状态统计图',
          left: 'center',
        },
        tooltip: {
          trigger: 'item',
        },
        legend: {
          top: '5%',
          orient: 'vertical',
          left: 'left',
        },
        series: [
          {
            type: 'pie',
            radius: ['40%', '70%'],
            avoidLabelOverlap: false,
            label: {
              show: '居民账号状态',
              position: 'center',
            },
            emphasis: {
              label: {
                show: true,
                fontSize: '40',
                fontWeight: 'bold',
              },
            },
            labelLine: {
              show: false,
            },
            data: [
              { value: this.offStatus, name: '被拉黑账号' },
              { value: this.onStatus, name: '正常账号' },
            ],
          },
        ],
      }
      myChart.setOption(option)
    },
    //获得地址信息
    async getAddressList() {
      const { data: res } = await this.$axios.post('/address/allAddress')
      if (res.status !== 0) {
        return this.message.error('获取地址信息列表失败！')
      }
      this.addressList = res.data

      var nums = new Array(16).fill(0)
      this.addressList.forEach((item) => {
        for (let i = 0; i < this.areas.length; i++) {
          if (item.area === this.areas[i]) nums[i]++
        }
      })
      this.nums = nums
      this.initAddressCharts()
    },
    //生成居民账号状态信息图表
    initAddressCharts() {
      var myChart = echarts.init(document.getElementById('addressChart'))
      var option = {
        title: {
          text: '上海各区发生火警次数统计图',
          left: 'center',
        },
        tooltip: {
          trigger: 'item',
        },
        grid: {
          left: '3%',
        },
        xAxis: {
          type: 'category',
          data: this.areas,
          nameTextStyle: {
            fontSize: 20,
          },
          axisLabel: {
            fontSize: '12',
            interval: 0,
          },
        },
        yAxis: {
          type: 'value',
        },
        series: [
          {
            data: this.nums,
            type: 'bar',
          },
        ],
      }
      myChart.setOption(option)
    },
  },
}
</script>
<style lang="less" scoped>
.el-card {
  margin-top: 20px;
}
.charts {
  display: flex;
}
.resChartCard {
  width: 600px;
  flex: 3;
  margin-right: 15px;
}
.adrChartCard {
  width: 1000px;
  flex: 5;
}
</style>